.imageLoading {
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    -webkit-box-align: center;
    display: inline-flex;
}

.image {
    height: 210px !important;
    width: 100% !important;
    transition: 0.3s ease !important;
    object-fit: cover !important;

    @media (min-width: 992px) {
        height: 150px !important;
    }
}

.description {
    line-height: 18px;
    text-align: justify;
    display: -webkit-box;
    overflow: hidden;

    a {
        color: unset !important;
    }
}