.wrapper {
    display: flex;
    float: right;
    margin-left: auto;
    overflow: hidden;
}

.itemWrapper {
    align-items: center;
    height: 100%;
    padding: 0 10px;
    cursor: pointer;
    transition: all .3s;
    justify-content: center;
    -webkit-box-align: center;
    display: inline-flex;
}